.item {
    width: 31.63%;
    position: relative;
    
    &__link {
        &::before {
            content: '';
    
            position: absolute;
    
            bottom: 6px;
            right: 0;
    
            width: 63px;
            height: 63px;
    
            background-color: $button-color;
            @include bg-img("../img/icon-right-arrow.svg");
            background-position: 50% 50%;
        }
    }
    
    &__title {
        position: absolute;
        
        top: 52px;
        left: 44px;
        
        max-width: 160px;
        @include pm-zero();
        font-family: "Roboto Condensed", sans-serif;
        @include font(20px, 400, 30px);
    }
}

@media (max-width: $table-width) {
    .item {
        width: 100%;

        &__image-box {
            width: 100%;
        }

        & img {
            width: 100%;
            height: auto;
        }

        &__title {
            top: 40px;

            font-size: 20px;
            line-height: 30px;
        }
    }
}

@media (max-width: $mobile-width) {
    .item {
        &__title {
            top: 20px;
            left: 34px;

            font-size: 20px;
            line-height: 30px; 
        }
    }
}