.popup-form {
    &__list {
        width: 100%;
        @include pm-zero();

        list-style: none;
    }

    &__item-info {
        margin-bottom: 30px;
    }

    &__field-info {
        box-sizing: border-box;

        width: 100%;
        padding-top: 14px;
        padding-bottom: 20px;
        padding-left: 24px;
    
        background-color: $background-field;
        color: $text-color;

        border: none;

        &--question {
            width: 100%;
            height: 104px;
            resize: none;
        }
        
        &::placeholder {
            color: $text-color;
        }
    }

    &__wrapper {
        display: flex;
        flex-direction: column-reverse;
    }

    &__item-consent {
        max-width: 310px;
        @include pm-zero();
        margin-bottom: 37px;
        padding-left: 48px;

        font-size: 14px;
        line-height: 16px;;
        color: $text-color;
        
        cursor: pointer;
    }

    &__label-consent {
        position: relative;
        cursor: pointer;

        &::before {
            content: "";

            position: absolute;
            top: 1px;
            left: -48px;

            box-sizing: border-box;
            width: 16px;
            height: 16px;

            border: 1px solid $consent-border;
        }
    }

    &__field-consent:checked ~ &__label-consent::before {
        @include bg-img("../img/icon-checked.svg");
        border: none;
    }

    &__button {
        margin-top: -5px;
        margin-bottom: 35px;

        @include button(100%);
        
        cursor: pointer;
    }
}