.popup {
    display: none;

    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    background-color: $background-popup;

    &__wrapper {
        position: relative;
        top: 135px;
        left: calc(50% - 245px);

        box-sizing: border-box;
        max-width: 490px;
        padding-left: 40px;
        padding-right: 40px;
        background-color: $background-form;
    }

    &__button {
        position: absolute;
        right: 0;

        width: 57px;
        height: 57px;

        background: none;
        @include bg-img("../img/icon-cross.svg");
        background-position: 50% 50%;

        border: none;
        cursor: pointer;
    }

    &__title {
        @include pm-zero();
        margin-bottom: 12px;
        padding-top: 30px;

        font-family: "Roboto Condensed", sans-serif;
        @include font(28px, 400, 40px);
        color: $text-color;
    }

    &__description {
        max-width: 370px;
        @include pm-zero();
        margin-bottom: 32px;
        
        color: $text-color;
    }

    &--open {
        display: block;
    }
}